import {src} from "./utils/dataGenerate";

const s = Snap("#svg");

function cr() {
    s.attr({width: 800, height: 800});
    s.attr({viewBox: "-400 -400 800 800"});
    const radius = 20;

    const styleCircle = {stroke: '#929292', 'strokeWidth': 1, fill: '#ffffff'}
    const styleLine = {stroke: '#929292', 'strokeWidth': 1, fill: 'none'}

    const circleR = (x, y) => {
        s.circle(x, y, radius)
            .attr(styleCircle);
    }


    s.circle(0, 0, 150).attr({stroke: '#a91ea3', 'strokeWidth': 1, fill: 'transparent'});
    s.circle(0, 0, 300).attr({stroke: '#a91ea3', 'strokeWidth': 1, fill: 'transparent'});

    let elem = document.getElementById('elem');

    const makeInfo = (item) =>{
        const name = `${item.name}`
        const start = `start-end: ${item.start} - ${item.end}`
        const center = `center: ${item.center}`
        const pos = `posX-posY: ${item.posX} - ${item.posY}`

        const data = `<div class="info"><div>${name}</div><div>${start}</div><div>${center}</div><div>${pos}</div></div>`
        elem.insertAdjacentHTML('beforeend', data);
    }

    const makeSVG = (item, lineStartX, lineStartY,) => {


        s.line(lineStartX, lineStartY, item.posX, item.posY).attr(styleLine);
        circleR(item.posX, item.posY);

        s.text(item.posX, item.posY, item.center);
    }

    const makeTriangle = (item, topX, topY,) => {

        s.polygon(topX,topY,item.posX,item.posX,100,100).attr(styleCircle);
    }


    src.map(item => {

        item.children.map(el => {
            // makeInfo(el)

            makeTriangle(el, item.posX, item.posY)
            // makeSVG(el, item.posX, item.posY)
        })
        // makeInfo(item)
        // makeSVG(item, 0, 0)

        makeTriangle(item, 0, 0)
    })
}

cr()
